// Breakpoints
$mobile: 484px;
$tablet: 768px;
$desktop: 1024px;
$DEBUG: false;

html,
body {
  background-color: #fff;
}

.App {
  width: $mobile;
  margin: 0 auto;
  margin-top: 10%;
  @if $DEBUG {
    border: 1px solid red;
  }
}


@media only screen and (min-width: $tablet) {
  .App {
    width: 768px; 
    
    @if $DEBUG {
      border-color: yellow;
    }
  }
}

@media only screen and (min-width: $desktop) {
  .App {
    width: $desktop; 
    
    @if $DEBUG {
      border-color: green;
    }

  }
}